.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.SiteNav {
  display: flex;
}

.overlay {
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* top:0; */
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  z-index:1;
  margin-left: auto;
}

.headerContainer{
  height: 100%;
  width: 100%;
  overflow: hidden;
}

h1{
  color:#fff;
}

body{
  background-color: black;
}